import {Dispatch, SetStateAction} from 'react';

import {GamePopup} from '../../../../layout/game-popup/game-popup';
import gamePopupStyle from '../../../../layout/game-popup/game-popup.scss';
import {Locale, useLocale} from '../../../../provider/locale/locale-context';
import {UiButton} from '../../../../layout/ui-button/ui-button';
import {uiButtonImage} from '../../../../layout/ui-button/image/ui-button-image';
import {useGameContext} from '../../../../provider/game/game-context';
import {getMapById} from '../../../../model/map/map-helper';

type ClientGameStartGamePopupPropsType = {
    isGameStarMessageOpen: boolean;
    setIsGameStarMessageOpen: Dispatch<SetStateAction<boolean>>;
};

export function ClientGameStartGamePopup(props: ClientGameStartGamePopupPropsType): JSX.Element | null {
    const {isGameStarMessageOpen, setIsGameStarMessageOpen} = props;
    const {gameState} = useGameContext();
    const {mapId} = gameState;
    const mapById = getMapById(mapId);
    const {getLocalizedString} = useLocale();

    if (!mapById) {
        return null;
    }

    const {mapNameLangKey, mapObjectiveLangKey} = mapById;
    const mapName: string = getLocalizedString(mapNameLangKey).trim();
    const mapObjective: string = getLocalizedString(mapObjectiveLangKey).trim();

    return (
        <GamePopup isOpen={isGameStarMessageOpen}>
            <h3 className={gamePopupStyle.game_popup__header}>
                {mapName || <Locale stringKey="NOTIFICATION__OBJECTIVE" />}
            </h3>
            <p className={gamePopupStyle.game_popup__text}>
                {mapObjective || <Locale stringKey="NOTIFICATION__SKIRMISH_OBJECTIVE" />}
            </p>
            <div className={gamePopupStyle.game_popup__footer}>
                <UiButton
                    iconSrc={uiButtonImage.sword.src}
                    langKey="SETUP_SKIRMISH__FIGHT"
                    onClick={() => setIsGameStarMessageOpen(false)}
                />
            </div>
        </GamePopup>
    );
}
