import {Dispatch, SetStateAction} from 'react';

import {GamePopup} from '../../../../layout/game-popup/game-popup';
import gamePopupStyle from '../../../../layout/game-popup/game-popup.scss';
import {Locale, useLocale} from '../../../../provider/locale/locale-context';
import {UiButton} from '../../../../layout/ui-button/ui-button';
import {useGameContext} from '../../../../provider/game/game-context';
import {getMapById} from '../../../../model/map/map-helper';
import {Markdown} from '../../../../layout/markdown/markdown';
import {LangKeyType} from '../../../../provider/locale/translation/type';
import {snackbarUiImage} from '../../../../layout/game-snackbar/image/sknackbar-image';

type ClientGameCompanyStoryPopupPropsType = {
    isGameCompanyStoryOpen: boolean;
    setIsGameCompanyStoryOpen: Dispatch<SetStateAction<boolean>>;
};

export function ClientGameCompanyStoryPopup(props: ClientGameCompanyStoryPopupPropsType): JSX.Element | null {
    const {isGameCompanyStoryOpen, setIsGameCompanyStoryOpen} = props;
    const {gameState} = useGameContext();
    const {mapId} = gameState;
    const mapById = getMapById(mapId);
    const {getLocalizedString} = useLocale();

    if (!mapById) {
        return null;
    }

    const {storyMarkdownLangKeyList, mapNameLangKey} = mapById;
    const storyMarkdownList: Array<JSX.Element> = storyMarkdownLangKeyList.map<JSX.Element>(
        (storyLangKey: LangKeyType): JSX.Element => {
            return (
                <Markdown
                    className={gamePopupStyle.game_popup__story_markdown_item}
                    key={storyLangKey}
                    mdInput={getLocalizedString(storyLangKey)}
                />
            );
        }
    );

    if (storyMarkdownList.length === 0) {
        return null;
    }

    return (
        <GamePopup isOpen={isGameCompanyStoryOpen}>
            <h3 className={gamePopupStyle.game_popup__header}>
                <Locale stringKey={mapNameLangKey} />
            </h3>
            <div className={gamePopupStyle.game_popup__story_markdown_list}>{storyMarkdownList}</div>
            <div className={gamePopupStyle.game_popup__footer}>
                <UiButton
                    iconSrc={snackbarUiImage.close.src}
                    langKey="UI__CLOSE"
                    onClick={() => setIsGameCompanyStoryOpen(false)}
                />
            </div>
        </GamePopup>
    );
}
