import {useEffect, useState} from 'react';

import {useGameContext} from '../../provider/game/game-context';
import {GameActionTypeEnum} from '../../provider/game/game-context-type';
import {noop} from '../../util/function';

import {GameFacedSnackbar} from './game-faced-snackbar';
import {GameFacedSnackbarType} from './game-faced-snackbar-type';

export function GameActionFacedSnackbar(): JSX.Element {
    const {pushAction, animationList} = useGameContext();
    const first = animationList.at(0);
    const [isOpen, setIsOpen] = useState<boolean>(false);
    const [dataList, setDataList] = useState<Array<GameFacedSnackbarType>>([]);
    const [handleHideLastItem, setHandleHideLastItem] = useState<{callback: typeof noop}>({callback: noop});

    useEffect(() => {
        if (!first) {
            return;
        }

        const {action, animationOnFinish} = first;

        if (action.actionType !== GameActionTypeEnum.showBriefing) {
            return;
        }

        if (action.briefing?.briefingList?.length) {
            setDataList(action.briefing.briefingList);
            setIsOpen(true);

            setHandleHideLastItem({
                callback: () => {
                    setIsOpen(false);
                    animationOnFinish();
                    if (action.briefing?.onBriefingEnd) {
                        pushAction(action.briefing?.onBriefingEnd);
                    }
                },
            });
            return;
        }

        animationOnFinish();
    }, [pushAction, first]);

    return (
        <GameFacedSnackbar
            dataList={dataList}
            isOpen={isOpen}
            onHideLastItem={handleHideLastItem.callback}
            refId="from-action"
        />
    );
}
