/* global document, HTMLDivElement, KeyframeAnimationOptions, Keyframe */

import {Dispatch, SetStateAction, useEffect, useRef, useState} from 'react';
import {createPortal} from 'react-dom';

import {animationStepTime} from '../../../page/client/client-game/client-game-effect/client-game-effect-helper';
import {UiButton} from '../../ui-button/ui-button';
import {Markdown} from '../../markdown/markdown';
import {useLocale} from '../../../provider/locale/locale-context';
import {GameFacedSnackbarType} from '../game-faced-snackbar-type';
import {snackbarUiImage} from '../../game-snackbar/image/sknackbar-image';

import facedSnackbarItemStyle from './game-faced-snackbar-item.scss';

type GameSnackbarPropsType = {
    facedData: GameFacedSnackbarType;
    index: number;
    isOpenList: Array<boolean>;
    onHideLastItem: () => void;
    setIsOpenList: Dispatch<SetStateAction<Array<boolean>>>;
};

export function GameFacedSnackbarItem(props: GameSnackbarPropsType): JSX.Element | null {
    const {getLocalizedString} = useLocale();
    const {isOpenList, setIsOpenList, onHideLastItem, index, facedData} = props;
    const [isViewOpen, setIsViewOpen] = useState<boolean>(false);
    const contentNode = useRef<HTMLDivElement>(document.createElement('div'));
    const isOpen = isOpenList[index];

    useEffect(() => {
        const snackbarAnimationOptions: KeyframeAnimationOptions = {
            duration: animationStepTime * 2,
            easing: 'ease-in-out',
            fill: 'forwards',
            iterations: 1,
        };

        const contentKeyframeList: Array<Keyframe> = [
            {opacity: 0, transform: 'translate3d(-50%, 200%, 0)'},
            {opacity: 1, transform: 'translate3d(-50%, 0, 0)'},
        ];

        if (isOpen === true && isViewOpen === false) {
            contentNode.current.animate(contentKeyframeList, snackbarAnimationOptions).onfinish = () => {
                setIsViewOpen(true);
            };
            return;
        }

        if (isOpen === false && isViewOpen === true) {
            contentNode.current.animate([...contentKeyframeList].reverse(), snackbarAnimationOptions).onfinish = () => {
                setIsViewOpen(false);
            };
        }
    }, [isOpen, isViewOpen]);

    if (typeof document === 'undefined') {
        return null;
    }

    if (isOpen === false && isViewOpen === false) {
        return null;
    }

    return createPortal(
        <div className={facedSnackbarItemStyle.faced_snackbar__content} ref={contentNode}>
            <div className={facedSnackbarItemStyle.faced_snackbar__content_wrapper}>
                <img
                    alt={facedData.markdownLangKey}
                    className={facedSnackbarItemStyle.faced_snackbar__face}
                    src={facedData.faceSrc}
                />
                <Markdown
                    className={facedSnackbarItemStyle.faced_snackbar__markdown_item}
                    mdInput={getLocalizedString(facedData.markdownLangKey)}
                />
            </div>
            <UiButton
                iconSrc={snackbarUiImage.close.src}
                isSquared
                onClick={() => {
                    setIsOpenList((currentIsOpenList: Array<boolean>): Array<boolean> => {
                        const newIsOpenList: Array<boolean> = [...currentIsOpenList];
                        const nextIndex = index + 1;

                        newIsOpenList[index] = false;

                        if (nextIndex < currentIsOpenList.length) {
                            newIsOpenList[nextIndex] = true;
                        } else {
                            onHideLastItem();
                        }

                        return newIsOpenList;
                    });
                }}
            />
        </div>,
        document.body
    );
}
