    MAP_NAME__AE2_MISSION_2__BRIEFING_8_VALADORN:
        'Хорошая работа, Капитан. Но у нас нет времени на отдых. Кристалл Жизни все еще в опасности. Мы должны предупредить Короля Галамара!',
    MAP_NAME__AE2_MISSION_2__BRIEFING_9_SOLDIER: 'Да, Сэр. Я приготовлю войска.',

    // start
    MAP_NAME__AE2_MISSION_3__BRIEFING_1_SOLDIER:
        'Ваше величество, лес известен как обитель духов, элементалов, и других магических созданий. Путешествовать по нему ночью небезопасно.',
    MAP_NAME__AE2_MISSION_3__BRIEFING_2_GALAMAR:
        'Капитан, вы слышали посланника от Валадорна - мы должны поднажать! Ничего не должно стоять между нами и Храмом Жизни.',
    MAP_NAME__AE2_MISSION_3__BRIEFING_3_SOLDIER: 'Понял. Войска, держите глаза открытыми и защитите Короля Галамара!',
    // trigger
