    // end
    MAP_NAME__AE2_MISSION_6__BRIEFING_2_VALADORN:
        'Давайте поспешим на Север, чтобы встретиться с Галамаром. Враги знают о его планах и ему может потребоваться наша помощь!',

    // start
    // trigger
    MAP_NAME__AE2_MISSION_7__BRIEFING_1_DEMON_LORD: 'Беги, жалкий человечишко, пока еще не слишком поздно!',
    MAP_NAME__AE2_MISSION_7__BRIEFING_2_SOLDIER: 'Вражеские войска выглядят угрожающе, я рекомендую отступить.',
