        'Наконец-то, Галамар и Валадорн! Как вы храбры, чтобы противостоять мне! Все ключи на местах, и скоро величайшая сила из свех, когда либо созранных на земле подчинится моей воле. Ваше уничтожение будет доказательством этой силы!',
    MAP_NAME__AE2_MISSION_8__BRIEFING_3_VALADORN: 'Твои уловки больше не будут вводить нас в заблуждение, Саеф!',
    MAP_NAME__AE2_MISSION_8__BRIEFING_4_NO_IMAGE: 'ЦЕЛЬ ДОСТИГНУТА...\n\nНЕБЕСНАЯ ЯРОСТЬ АКТИВИРОВАНА!',
