    MAP_NAME__AE2_MISSION_8__BRIEFING_11_SAETH:
        'В твоей незначительной жизни и ограниченных мозгах, возможно...Тем не менее все сейчас в движении - невидимые силы, которые управляют временем за пределами твоих знаний. Ты не сможешь остановить меня от воскрешения!',
    MAP_NAME__AE2_MISSION_8__BRIEFING_12_SOLDIER: 'Ваше Величество, Руины разваливаются! Мы должны уйти немедленно!',
