/* global structuredClone */

import {GamePlayerIdEnum, GamePlayerType} from '../game/player/player-type';
import {GameUnitType} from '../game/unit/unit-type';
import {GameBuildingType} from '../game/building/building-type';
import {getBuildingInfo} from '../game/building/building-helper';
import {getUnitInfoByUnit} from '../game/unit/unit-helper';
import {defaultPlayerData} from '../game/player/player-const';
import {GameStateType} from '../game/game-type';
import {startedMoneyList, unitLimitList} from '../game/game-const';
import {GameLandscapeType} from '../game/landscape/landscape-type';

import {GameMapType, MapTypeEnum} from './map-type';
import {companiesAe2MapList, skirmishMapList} from './map-const';
import {getUserMapList} from './user-map';

export function getMapById(mapId: string): GameMapType | null {
    const map: GameMapType | void = [...skirmishMapList, ...getUserMapList(), ...companiesAe2MapList].find<GameMapType>(
        (mapInList: GameMapType): mapInList is GameMapType => {
            return mapInList.mapId === mapId;
        }
    );

    if (!map) {
        console.error(`can not find map by id: ${mapId}`);
        return null;
    }

    return map;
}

export function getIsSkirmishMap(mapId: string): boolean {
    if (!mapId) {
        return false;
    }

    const map = getMapById(mapId);

    return map?.mapType === MapTypeEnum.skirmish;
}

export function getIsCompaniesMap(mapId: string): boolean {
    if (!mapId) {
        return false;
    }

    const map = getMapById(mapId);

    return map?.mapType === MapTypeEnum.companies;
}

export function getShortMapInfoById(mapId: string): Record<'hasBriefing' | 'hasStory', boolean> {
    const map = getMapById(mapId);

    if (!map) {
        return {hasBriefing: false, hasStory: false};
    }

    const hasStory = map.storyMarkdownLangKeyList.length > 0;
    const hasBriefing = map.briefingList.length > 0;

    return {hasBriefing, hasStory};
}

function getPlayerIdSetByMap(map: GameMapType): Set<GamePlayerIdEnum> {
    const playerIdSet: Set<GamePlayerIdEnum> = new Set<GamePlayerIdEnum>();
    const {unitList, buildingList} = map;

    unitList.forEach((unitInList: GameUnitType) => {
        if (getUnitInfoByUnit(unitInList).isHero) {
            playerIdSet.add(unitInList.gamePlayerId);
        }
    });

    buildingList.forEach((buildingInList: GameBuildingType) => {
        if (
            getBuildingInfo(buildingInList).canBeStore &&
            buildingInList.gamePlayerId !== GamePlayerIdEnum.playerEmpty
        ) {
            playerIdSet.add(buildingInList.gamePlayerId);
        }
    });

    return playerIdSet;
}

export function getMaxPlayerByMap(map: GameMapType): number {
    return getPlayerIdSetByMap(map).size;
}

export function extractPlayerListByMap(map: GameMapType): Array<GamePlayerType> {
    const playerIdSet: Set<GamePlayerIdEnum> = getPlayerIdSetByMap(map);

    return [...playerIdSet].map<GamePlayerType>((playerId: GamePlayerIdEnum): GamePlayerType => {
        return {...defaultPlayerData[playerId]};
    });
}

export function gameMapToGameState(
    map: GameMapType,
    partialGameState: Pick<GameStateType, 'clientGamePlayerIdList' | 'playerList'>
): GameStateType {
    const {playerList, clientGamePlayerIdList} = partialGameState;

    const gameState: GameStateType = {
        activeGamePlayerId: clientGamePlayerIdList.at(0) || GamePlayerIdEnum.player1,
        buildingList: structuredClone<Array<GameBuildingType>>(map.buildingList),
        cemetery: [],
        clientGamePlayerIdList,
        doneTriggerIsList: [],
        gameSettings: {
            startedMoney: startedMoneyList[0],
            unitLimit: unitLimitList[0],
        },
        graveList: [],
        landscape: structuredClone<GameLandscapeType>(map.landscape),
        mapId: map.mapId,
        playerList,
        stepCount: 0,
        unitList: structuredClone<Array<GameUnitType>>(map.unitList),
    };

    return gameState;
}
