import {Page} from '../../../layout/page/page';
import {PageHeader} from '../../../layout/page/page-header/page-header';
import {PageContent} from '../../../layout/page/page-content/page-content';
import {companiesAe2MapList} from '../../../model/map/map-const';
import {GameMapType} from '../../../model/map/map-type';

import {CompanySaveStateType, getCompanySaveState} from '../../../provider/game/action/companies-action';

import clientCompaniesStyle from './client-companies.scss';
import {defaultCompanyState} from './client-companies-const';
import {ClientCompanyItem} from './client-company-item/client-company-item';

export function ClientCompanies(): JSX.Element {
    const companySaveState: CompanySaveStateType = getCompanySaveState();

    return (
        <Page>
            <PageHeader langKey="UI__BUTTON__COMPANIES" />
            <PageContent>
                <div className={clientCompaniesStyle.client_companies__map_list}>
                    {companiesAe2MapList.map<JSX.Element>(
                        (map: GameMapType, index: number, list: Array<GameMapType>): JSX.Element => {
                            // actually first mission in map list, always ready to play
                            if (index === 0) {
                                return (
                                    <ClientCompanyItem
                                        companyState={defaultCompanyState}
                                        isAvailable
                                        key={map.mapId}
                                        map={map}
                                    />
                                );
                            }

                            const prevMissionMap: GameMapType = list[index - 1];

                            const prevMissionMapId: string = prevMissionMap.mapId;

                            const isPrevMissionMapDone: boolean = companySaveState[prevMissionMapId]?.isDone || false;

                            return (
                                <ClientCompanyItem
                                    companyState={defaultCompanyState}
                                    isAvailable={isPrevMissionMapDone}
                                    key={map.mapId}
                                    map={map}
                                />
                            );
                        }
                    )}
                </div>
            </PageContent>
        </Page>
    );
}
